import React, { Component } from "react";
import { observer, inject } from "mobx-react";

class TableControl extends Component {
  renderRow = (data, index) => {
    let cells = [];
    cells.push(<td key={"rowIndex"}>{index + 1}</td>);
    for (let key in data) {
      if (this.props.tableStore.columnsToShow[key]) {
        cells.push(<td key={key}>{data[key]}</td>);
      }
    }
    return <tr key={data.id}>{cells}</tr>;
  };
  render() {
    if (!this.props.tableStore.data) {
      return null;
    }
    let thead = [];
    thead.push(<th key={"rowNum"}>№</th>);
    for (let key in this.props.tableStore.data[0]) {
      if (this.props.tableStore.columnsToShow[key]) {
        thead.push(
          <th key={key} onClick={() => this.props.tableStore.sort(key)}>
            {key}
          </th>
        );
      }
    }
    return (
      <table className="ui celled table">
        <thead>
          <tr>{thead}</tr>
        </thead>
        <tbody>
          {this.props.tableStore.data.map((data, index) => {
            return this.renderRow(data, index);
          })}
        </tbody>
      </table>
    );
  }
}

export default inject((stores) => ({
  tableStore: stores.rootStore.tableStore,
}))(observer(TableControl));
