import React, { Component } from "react";
import { inject, observer } from "mobx-react";

class TableControl extends Component {
  handleChange = (key) => {
    this.props.tableStore.changeColumnShowStatus(key);
  };

  render() {
    if (this.props.tableStore.data) {
      let checkboxes = [];
      for (let key in this.props.tableStore.data[0]) {
        checkboxes.push(
          <div className="ui checkbox" key={key}>
            <input
              type="checkbox"
              name={key}
              onChange={() => {
                this.handleChange(key);
              }}
              checked={this.props.tableStore.columnsToShow[key]}
            />
            <label>{key}</label>
          </div>
        );
      }
      return <div className="ui segment">{checkboxes}</div>;
    }
    return null;
  }
}

export default inject((stores) => ({
  tableStore: stores.rootStore.tableStore,
}))(observer(TableControl));
