import { observable, decorate, when, computed } from "mobx";

class TableStore {
  data = null;
  columnsToShow = {};
  constructor(rootStore) {
    this.rootStore = rootStore;

    // Show all columns when data recieved
    when(
      () => this.data,
      () => {
        this.showAllColumns();
      }
    );
  }

  get canRemoveColumn() {
    let shownColumnsCount = 0;
    for (let key in this.columnsToShow) {
      if (this.columnsToShow[key]) {
        shownColumnsCount++;
      }
      if (shownColumnsCount > 1) {
        return true;
      }
    }
    return false;
  }
  sort = (column) => {
    this.data = this.data.sort((postLeft, postRight) => {
      if (postLeft[column] < postRight[column]) {
        return -1;
      }
      if (postLeft[column] > postRight[column]) {
        return 1;
      }
      return 0;
    });
  };
  showAllColumns = () => {
    for (let key in this.data[0]) {
      this.columnsToShow[key] = true;
    }
  };

  changeColumnShowStatus = (key) => {
    if (this.columnsToShow[key] && !this.canRemoveColumn) {
      return;
    }
    this.columnsToShow[key] = !this.columnsToShow[key];
  };
}

decorate(TableStore, {
  data: observable,
  columnsToShow: observable,
  canRemoveColumn: computed,
});

class RootStore {
  constructor() {
    this.tableStore = new TableStore(this);
  }
}

export default RootStore;
